
.ellipsis-list-3
{
	display:-webkit-box;
    -webkit-line-clamp: 3;
    overflow:hidden;
}
.yd-list-theme4 .yd-list-item .yd-list-img{
	display: none;
}
.reply-item-wrap{
	width:100%;
	margin:0 auto;
	position: relative;
}
.reply-item-wrap .highlight{
	color:#61a4e9;
}
.reply-item-wrap .header-img{
	width:.6rem;
	border-radius: 50%;
	vertical-align: middle;
}
.reply-item-wrap .send-name{
	font-size:.3rem;
	color:#818181;
	display: inline-block;
	width:2rem;
}
.reply-item-wrap li{
	margin:.1rem auto;
}
.reply-item-wrap .reply-item-2{
	font-size:.3rem;
	color:#a3a9b2;
	padding-left: .6rem;
}
.reply-item-wrap .reply-item-2 span{
	padding-right: .2rem;
}
.reply-item-wrap .reply-item-3{
	font-size:.4rem;
	color:black;
	padding-left:.6rem;
	padding-right:.9rem;
	line-height: 1.5em;
}
.reply-item-wrap .reply-item-4{
	font-size:.3rem;
	text-align: right;
}
.reply-item-wrap .time-img{
	width: .35rem;
    vertical-align: top;
}
.b{
	position: absolute;
    right: 0;
    bottom:.55rem;
    font-size:.3rem;
    color: #61a4e9;
    width:1rem;
}
.ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}



