
.qy-directory-group li{
	border-bottom:.02rem dotted #A1A8B1;
	padding: .2rem;
}
.qy-directory-group a{
	font-size:.4rem;
	color:#5b5b5b;
	vertical-align: middle;
	display:block;
	width:100%;
}
.qy-directory-group a.qy-read-static{
	color:#9FA7B0;
}
.qy-directory-group .yd-badge-hollow:after,.qy-directory-group .yd-badge-hollow{
	border-color:#f46094;
	color:#f46094;
}
.qy-directory-group .qy-chapter{
	padding-left:2em;
}
.qy-directory-group .ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.qy-directory-group .chapter-text{
	max-width:85%;
	display: inline-block;
	vertical-align: middle;
}



