
.ellipsis-list-2{
    box-orient:vertical;
	display:-webkit-box;
    -webkit-line-clamp: 2;
    overflow:hidden;
}
.max-width{
	width:90%;
	margin:0 auto;
	overflow:hidden;
}
.comment-wrap{
	border-bottom:.02rem solid #8c929b;
	margin:.6rem 0 0;
	padding-bottom:.2rem;
}
.content{
	font-size:.4rem;
	font-weight:600;
}
.book-img{
	height:3rem;
    border-radius:0.2rem;
    margin-right:.2rem;
}
.author-img{
	width:.6rem;
	border-radius:50%;
	vertical-align:middle;
}
.time-img{
	width: .35rem;
    vertical-align: top;
}
.user-img{
	width:.6rem;
	border-radius:50%;
	vertical-align:bottom;
}
.like-list span{
	font-size:.4rem;
	color:#8c929b;
}
.book-name{
	font-size:.4rem;
	color:black;
}
.book-detail-group{
	font-size:.3rem;
	color:#8c929b;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	margin:.2rem auto;
}
.book-detail li{
	margin:.1rem 0 .2rem;
}
.left{
	float: left;
	color:#8c929b;
	font-size:.3rem;
}
.right{
	float: right;
	color:#8c929b;
	font-size:.3rem;
}
.wrap{
	background:#fff;
	margin-bottom:.2rem;
}
.like-list{
	padding-top:.2rem;
}



