
.send-wrap{
	position:fixed;
	bottom:0;
	z-index:999;
	border-top:.02rem solid #b2b2b2;
	width:100%;
}
.send-wrap .yd-cell-box{
	margin-bottom: 0;
}
.send-wrap .yd-cell-left{
	width:80%;
}
.send-wrap .yd-cell:after{
	border:0;
}
.send-wrap .yd-cell-right span{
	font-size:.9rem;
}
.send-wrap .checked{
	color:#f46094;
}
.send-wrap .send-btn-img{
	width:.8rem;
	padding:.2rem 0;
}
.send-wrap .face-wrap{
	background: #fff;
    padding: .04rem .24rem;
    width: 100%;
    border-bottom:.02rem solid #b2b2b2;
}
.send-wrap .yd-input>input{
	font-size: .4rem;
}		



