
.yd-cell:after{border:0;
}
.yd-cell-right input[type]{
	font-size:.4rem;
}
.qy-cell-item{
	border:.02rem solid #bfbfbf;
	border-radius:.8rem;
	margin-bottom: .4rem;
	position: relative;
}
.yd-cell-left{
	padding-right:.2rem
}
.icon-img{
	width:.4rem;
	vertical-align: top;
}
.send-colde-btn{
	width:2.2rem;
	margin-left:-1rem;
	padding:0;
	border-radius: .3rem;
	background: #fff;
	border:1px solid #f46094;
	color:#f46094;
	font-size:.4rem;
}
.qy-cell-item .yd-btn-disabled{
	background-color: #ccc;
    color: #f0f0f0;
    pointer-events: none;
    border: 0;
}



